* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'PT Sans', sans-serif;
  }
  
  .home,
  .services,
  .products,
  .sign-up {
    display: flex;
    height: 90vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
  }
  
  .services {
    /* background-image: url('/images/img-2.jpg'); */
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }
  
  .products {
    /* background-image: url('/images/img-1.jpg'); */
    background-position: center;
    background-size: fill;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }
  
  .sign-up {
    /* background-image: url('/images/img-8.jpg'); */
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }


  /* about us section */

  .about {
    text-align: center;
    padding-left: 5%;
    padding-right: 5%;
  }
  
  .about-header {
    /* background-color: #282c34; */
    min-height: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    /* color: white; */
  }

  .about-header h2 {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  }

  .about-header h4 {
    margin-top: 32px;
    font-size: 18px;
  }
  .about-header p {
    margin-top: 12px;
    text-wrap: balance;
    justify-content: start;
    font-size: 18px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
      'Lucida Sans', Arial, sans-serif;
  }
  

  /* FAQ section */

  .faq-main {
    margin-top: 64px;
    text-align: center;
    font-family: Arial, sans-serif;
  }
  
  .faqs {
    max-width: 800px;
    margin: 20px auto;
    text-align: left;
  }
  
  .faq {
    background: #f9f9f9;
    margin-bottom: 10px;
    padding: 10px 20px;
    cursor: pointer;
  }
  
  .faq.open .faq-answer {
    padding: 10px 0;
  }
  
  .faq-question {
    margin: 0;
    font-weight: bold;
  }
  
  .faq-answer {
    display: none;
    font-size: small;
    font-style: italic;
  }
  
  .faq.open .faq-answer {
    display: block;
    color: #333;
  }
  
  /* contact us section */

  .App {
    text-align: center;
    font-family: 'Arial', sans-serif;
    padding: 50px;
  }
  
  .contact-form {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
    background: #f3f3f3;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .contact-form h1 {
    margin-bottom: 20px;
  }
  
  .contact-form input, .contact-form textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .contact-form button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
  }
  
  .contact-form button:hover {
    background-color: #0056b3;
  }
  